import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PREGUNTAS } from './preguntasLista';
import { preguntasVo } from './preguntasVo';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'qrcaro';
  pregunta: preguntasVo = {"pregunta" : "" , respuestas : [], correcta: 0 };
  habilitado : boolean = true;
  respuestaCorrecta: string = '';

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.queryParams
      .subscribe(params => {
        console.log(params);

        this.pregunta = PREGUNTAS[Number.parseInt(params.pregunta) - 1];
        this.sleep(30000).then(() => { this.finTimer(); });
      }
    );
  }

  sleep(ms : number): any {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
  
  finTimer() {
    this.habilitado = false;
    this.respuestaCorrecta = "Se acabo el tiempo la respuesta es " + this.pregunta.respuestas[this.pregunta.correcta];
  }

  clickrespuesta(resp : string) {
    console.log("clic " + resp);
    this.habilitado = false;
    if (this.pregunta.respuestas[this.pregunta.correcta] != resp) {
      this.respuestaCorrecta = "Fallo la respuesta es " + this.pregunta.respuestas[this.pregunta.correcta];
    } else {
      this.respuestaCorrecta = "La respuesta es correcta!";
    }
  }
}
